@import '../../styles/vars';
@import '../../styles/mixins';

.Loader {
  display: grid;
  place-items: center;
  width: 100%;
  min-height: 400px;

  &__content {
    border-radius: 50%;
    width: 100px;
    height: 100px;
    border: 25px solid #ddd;
    border-left-color: #000;
    animation: load8 1.2s infinite linear;
  }
}

@keyframes load8 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
