@import '../../styles/vars';
@import '../../styles/mixins';
@import '../../styles/button';
@import '../../styles/selection-off';

.slider {
  &__hBLock {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &--buttons {
      display: flex;
      gap: 16px;
    }

    &--buttons-imgLeft {
      rotate: 180deg;
    }

    &--title {
      @media (max-width: 340px) {
        width: 130px;
      }
    }
  }

  &__list {
    display: flex;
    justify-content: center;
    margin-top: 24px;

    &--container {
      display: flex;
      box-sizing: border-box;
      gap: 16px;
      padding: 0;
      margin: 0;
      list-style-type: none;
      overflow: hidden;
      width: 212px;

      @include on-big-mobile {
        width: 440px;
      }

      @include on-tablet {
        width: 490px;
      }

      @include on-big-tablet {
        width: 743px;
      }

      @include on-desktop {
        width: initial;
      }
    }
  }
}
